import { makeStyles, withStyles } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'

export const useStyles = makeStyles((theme) => ({
  root: {
    padding: '50px 0px',
    maxWidth: '1220px',
    margin: 'auto',
  },
  footerContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  iconSpan: {
    display: 'inline-block',
    width: '10px',
    height: '10px',
    backgroundColor: '#4BDC4B',
    borderRadius: '10px',
    marginRight: '10px',
  },
  footerLeft: {
    display: 'flex',
    gap: '20px',
  },
  footerRight: {
    display: 'flex',
    gap: '40px',
  },
  socialBtn: {
    '&:hover': {
      opacity: '.5%',
    },
  }
}))

export const StyledContainer = withStyles((theme) => ({
  root: {
    padding: '0 40px 0px 40px',
  },
}))(Container)
