import React, { useState, useRef, useEffect, createRef } from 'react'
import { de } from 'date-fns/locale'
import { ChartTooltip } from './ChartTooltip'
import { Line } from 'react-chartjs-2'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js'

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
)

function numberWithCommas(num: number) {
  return num
    .toFixed(2)
    .toString()
    .replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

export const NewChartCalc = (props: any) => {
  let tooltipRef = useRef<HTMLDivElement>()
  const getGradient = () => {
    let canvas = document.createElement('canvas') as HTMLCanvasElement
    let ctx = (canvas.getContext('2d') as unknown) as CanvasRenderingContext2D
    let gradient = ctx.createLinearGradient(0, 0, 0, 400)
    gradient.addColorStop(0, 'rgba(10, 147, 150, 0.21)')
    gradient.addColorStop(0.8, 'rgba(255, 255, 255, 0)')
    return gradient
  }

  const convertDate = (timeStamp: number) => {
    const date = new Date(timeStamp)
    const realDate = `${date.getFullYear()}-${
      date.getMonth() > 9
        ? Number(date.getMonth()) + 1
        : '0' + Number(date.getMonth())
    }-${
      date.getDay() > 9
        ? Number(date.getDay()) + 1
        : '0' + Number(date.getDay())
    }T${date.getHours() > 9 ? date.getHours() : '0' + date.getHours()}:${
      date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes()
    }:${
      date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds()
    }.${date.getMilliseconds()}Z`

    return realDate
  }

  const getDataTraditional = (rate: number, years: number, amount: number) => {
    const days = years * 365 + 60
    const now = Date.now()
    var newDate = now
    var finalArray = []
    var runningTotal = amount
    var i = 0
    while (i <= days) {
      if (i === 0) {
        finalArray.push({
          x: convertDate(newDate),
          y: runningTotal,
        })
      }
      if (i % 30 === 0) {
        finalArray.push({
          x: convertDate(newDate),
          y: runningTotal,
        })
      }
      runningTotal += runningTotal * rate
      newDate = newDate + 86400

      i++
    }
    return finalArray
  }

  const getData = (rate: number, years: number, amount: number) => {
    const days = years * 365
    const now = Date.now()
    var newDate = now
    var finalArray = []
    var runningTotal = amount
    var i = 0
    while (i <= days) {
      if (i === 0) {
        finalArray.push({ x: convertDate(newDate), y: runningTotal })
      }
      if (i % 30 === 0) {
        finalArray.push({ x: convertDate(newDate), y: runningTotal })
      }
      runningTotal += runningTotal * rate
      newDate = newDate + 86400

      i++
    }
    return finalArray
  }

  const data = {
    datasets: [
      {
        pointRadius: 0,
        data: getDataTraditional(0.000219178, props.years, props.amount),
        tension: 0.5,
        borderColor: 'rgb(0,0,0,)',
        borderWidth: 2,
        fill: { target: 'origin', above: getGradient() },
        backgroundColor: getGradient(),
      },
      {
        pointRadius: 0,
        data: getData(0.000509863, props.years, props.amount),
        tension: 0.5,
        borderColor: 'rgb(251, 216, 93)',
        borderWidth: 2,
        fill: { target: 'origin', above: getGradient() },
        backgroundColor: getGradient(),
      },
    ],
  }
  console.log(data)

  return (
    <div style={{ width: '100%' }}>
      <Line
        data={data}
        options={{
          maintainAspectRatio: false,
          responsive: true,
          plugins: {
            //@ts-ignore
            tooltip: {
              enabled: false,

              external: ({ chart, tooltip }) => {
                let element = tooltipRef.current!

                if (tooltip.opacity === 0) {
                  element.style.opacity = '0'
                  return
                }

                const div1 = document.getElementById('div3')
                const hr = document.getElementById('hr3')

                if (div1) {
                  try {
                    const i = tooltip.dataPoints[0].dataIndex
                    const isLast = i === data.datasets[0].data.length - 1
                    const item = data.datasets[0].data[i]
                    const deposits: number = item.y
                    const date = new Date(item.x)
                    div1.innerHTML = `
                    <span>$ ${numberWithCommas(
                      deposits,
                    )} UST ${date.toString().slice(0, 10)}
                    </span>`
                  } catch {}
                }
                if (hr) {
                  hr.style.top = chart.scales.y.paddingTop + 'px'
                  hr.style.height = chart.scales.y.height + 'px'
                }
                element.style.opacity = '1'
                element.style.transform = `translateX(${tooltip.caretX}px)`
              },
            },
            legend: { display: false },
          },
          interaction: {
            intersect: false,
            mode: 'index',
          },
          scales: {
            x: {
              offset: true,
              // type: 'time',
              time: {
                unit: 'minute',
                //@ts-ignore
                min: String(data.datasets[0].data[0].x),
                max: String(data.datasets.length && data.datasets[0].data[1].x),
              },
              bounds: 'data',
              ticks: {
                display: false,
                autoSkipPadding: 30,
              },
              grid: {
                display: false,
              },
            },
            y: {
              beginAtZero: false,
              ticks: {
                display: false,
              },
              grid: {
                display: false,
                drawBorder: false,
              },
            },
            //@ts-ignore
            //   xAxes: [
            //     {
            //       adapters: {
            //         date: {
            //           locale: de,
            //         },
            //       },
            //       type: 'time',
            //       time: {
            //         min: data.datasets[0].data[0].x,
            //         max: String(
            //           data.datasets.length && data.datasets[0].data[1].x,
            //         ),
            //       },
            //     },
            //   ],
          },
        }}
        height={343}
        width={'100%'}
        style={{ maxWidth: 'inherit' }}
      />
      <ChartTooltip ref={tooltipRef as React.RefObject<HTMLDivElement>} id="tt">
        <hr id="hr3" />
        <section
          id="div3"
          style={{
            backgroundColor: '#493C3C',
            padding: '5px 7px 5px 7px',
            fontSize: '10px',
            borderRadius: '20px',
            fontWeight: '400',
          }}
        ></section>
      </ChartTooltip>
    </div>
  )
}
