import React from 'react'
import { Link } from 'react-router-dom'
import { Grid, Typography, Box, InputBase } from '@material-ui/core'
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import { useStyles, StyledContainer } from './style'

import icon_twitter from '../../assets/twitter.png'
import icon_telegram from '../../assets/telegram.png'
import icon_discord from '../../assets/discord.png'
import icon_docs from '../../assets/docs.png'

function Footer() {
  var classes = useStyles()

  return (
    <footer>
      <div className={classes.root}>
        <div className={classes.footerContainer}>
          <div className={classes.footerLeft}>
            <div>
              <FiberManualRecordIcon style={{color: 'green', verticalAlign: 'middle', marginRight: 10}} />
              LATEST BLOCK: 28912928
            </div>
            <div>
              TERMS
            </div>
          </div>
          <div className={classes.footerRight}>
            <a className='socialBtn' href="https://twitter.com/anchor_protocol" target="_blank">
              <img
                src={icon_twitter}
                alt="twitter"
                style={{ width: '100%', height: 'auto' }}
              />
            </a>
            <a className='socialBtn' href="https://t.me/anchor_official" target="_blank">
              <img
                src={icon_telegram}
                alt="telegram"
                style={{ width: '100%', height: 'auto' }}
              />
            </a>
            <a className='socialBtn' href="https://discord.gg/9aUYgpKZ9c" target="_blank">
              <img
                src={icon_discord}
                alt="discord"
                style={{ width: '100%', height: 'auto' }}
              />
            </a>
            <a className='socialBtn' href="https://github.com/Anchor-Protocol" target="_blank">
              <img
                src={icon_docs}
                alt="docs"
                style={{ width: '100%', height: 'auto' }}
              />
            </a>
          </div>
        </div>
      </div>
    </footer>
  )
}

export default Footer
