import React, { useState, useRef, useEffect } from 'react'
import axios from 'axios'
import { ChartTooltip } from './ChartTooltip'
import { Line } from 'react-chartjs-2'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js'

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
)

function numberWithCommas(num: number) {
  return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

export function ANCPriceChart() {
  let tooltipRef = React.useRef<HTMLDivElement>()

  const [entireTVL, setEntireTVL] = useState({
    data: [
      { x: '2021-08-08T13:12:23', y: 3 },
      { x: '2021-08-08T13:12:45', y: 5 },
      { x: '2021-08-08T13:12:46', y: 6 },
      { x: '2021-08-08T13:13:11', y: 3 },
      { x: '2021-08-08T13:14:23', y: 9 },
      { x: '2021-08-08T13:16:45', y: 1 },
    ],
  })
  const MINUTE_MS = 15000

  useEffect(() => {
    const interval = setInterval(() => {
      axios.get('https://api.llama.fi/charts/terra').then(function (response) {
        // handle success
        setEntireTVL(response)
      })
    }, MINUTE_MS)

    return () => clearInterval(interval)
  }, [])

  useEffect(() => {
    axios.get('https://api.llama.fi/charts/terra').then(function (response) {
      // handle success
      setEntireTVL(response)
    })
  }, [])

  const getData = (histories: any) => {
    let finalArray: any = []

    histories.data.map((item: any, i: any) => {
      return finalArray.push({
        x: new Date(Number(item.date)).toString(),
        y: item.totalLiquidityUSD,
      })
    })

    return finalArray
  }

  const chartGradient = () => {
    // var ctx = document.getElementById('canvas').getContext("2d")
    // var gradient = ctx.createLinearGradient(0, 0, 0, 400)
    let canvas = document.createElement('canvas') as HTMLCanvasElement
    let ctx = (canvas.getContext('2d') as unknown) as CanvasRenderingContext2D
    let gradient = ctx.createLinearGradient(0, 0, 0, 400)
    gradient.addColorStop(0, 'rgba(10, 147, 150, 0.21)')
    gradient.addColorStop(0.8, 'rgba(255, 255, 255, 0)')
    return gradient
  }

  const data = {
    type: 'line',
    datasets: [
      {
        data: getData(entireTVL),
        tension: 0.5,
        borderColor: 'rgb(251, 216, 93)',
        borderWidth: 2,
        pointRadius: 0.5,
        width: '100%',
        fill: { target: 'origin', above: chartGradient() },
        backgroundColor: chartGradient(),
      },
    ],
  }

  return (
    <div style={{ marginTop: '-30px', width: 'inherit', position: 'relative' }}>
      <Line
        data={data}
        options={{
          maintainAspectRatio: false,
          responsive: true,
          plugins: {
            //@ts-ignore
            tooltip: {
              enabled: false,

              external: ({ chart, tooltip }) => {
                let element = tooltipRef.current!

                if (tooltip.opacity === 0) {
                  element.style.opacity = '0'
                  return
                }

                const div1 = document.getElementById('div2')
                const hr = document.getElementById('hr2')

                if (div1) {
                  try {
                    const i = tooltip.dataPoints[0].dataIndex
                    const isLast = i === data.datasets[0].data?.length - 1
                    const item = data?.datasets[0]?.data[i]
                    const deposits = item.y
                    const borrows = item.y
                    const date = new Date(item.x)

                    div1.innerHTML = `
                    <span>$ ${numberWithCommas(
                      deposits,
                    )} UST ${date.toString().slice(0, 10)}
                    </span>`
                  } catch {}
                }

                if (hr) {
                  hr.style.top = chart.scales.y.paddingTop + 'px'
                  hr.style.height = chart.scales.y.height + 'px'
                }

                element.style.opacity = '1'
                element.style.transform = `translateX(${tooltip.caretX}px)`
              },
            },

            legend: { display: false },
          },
          interaction: {
            intersect: false,
            mode: 'index',
          },

          scales: {
            x: {
              offset: true,

              // type: 'time',
              time: {
                unit: 'minute',
                //@ts-ignore
                min: String(data.datasets[0].data[0].x),
                // max: String(data.datasets[0].data.pop().x),
                max: String(data.datasets.length && data.datasets[0].data[1].x),
              },
              bounds: 'data',
              ticks: {
                display: false,
                autoSkipPadding: 30,
              },
              grid: {
                display: false,
              },
            },

            y: {
              beginAtZero: false,
              ticks: {
                display: false,
              },
              grid: {
                display: false,
                drawBorder: false,
              },
            },

            //@ts-ignore
          },
        }}
        height={320}
        width={'100%'}
        style={{ border: 'none' }}
      />
      <ChartTooltip ref={tooltipRef as React.RefObject<HTMLDivElement>} id="tt">
        <hr id="hr2" />
        <section
          id="div2"
          style={{
            backgroundColor: '#493C3C',
            padding: '5px 7px 5px 7px',
            fontSize: '10px',
            borderRadius: '20px',
          }}
        ></section>
      </ChartTooltip>
    </div>
  )
}
